/* 基本样式 */
.navbar-nav a {
  position: relative;
  color: #333; /* 设置链接颜色 */
  text-decoration: none; /* 移除默认下划线 */
  transition: color 0.3s ease-in-out; /* 颜色的过渡效果 */
}

/* 悬停状态下的颜色变化 */
.navbar-nav a:hover {
  color: #4527A0; /* 悬停时的链接颜色 */
}

/* 初始状态的下划线 */
.navbar-nav a::after {
  content: '';
  position: absolute;
  width: 100%;
  height: 3px; /* 下划线的高度 */
  bottom: 0;
  left: 0;
  background-color: #4527A0; /* 初始时的下划线颜色 */
  transform: scaleX(0); /* 初始时不可见 */
  transform-origin: bottom right;
  transition: transform 0.3s ease-out; /* 下划线的过渡效果 */
}

/* 悬停状态下的下划线 */
.navbar-nav a:hover::after {
  transform: scaleX(1); /* 变为完整的下划线 */
  transform-origin: bottom left;
}

